.nav{width:100%; position:fixed;top:0;left:0; background:#fff; border-bottom:1px solid #e8eaec; z-index:99;}
.nav ul{width:6.4rem;height:0.45rem; padding-top:0.15rem; margin:0 auto;}
.nav ul li{width:0.29rem;height:0.31rem; background:url(../img/history.png) no-repeat 0 0; background-size:100%; margin:0 0 0 0.38rem;}

.content{max-width:6.4rem; margin:0 auto; margin-top:-0.3rem; background:#ffff; padding-bottom:0.85rem;}
.content .header{ padding:0.39rem 0.28rem 0.15rem 0.38rem; height:auto; background: none}
.header h2{ width:0.5rem;float:left; margin-right:0.18rem;}
.header p{ float:left; margin-top:0.18rem; font-size:0.26rem;}

.content .cont{ padding:0 0.38rem; color:#494d4d;}
.content h3{ font-size:0.4rem; line-height:0.6rem; padding-bottom:0.25rem;padding:0 0.38rem; }
.cont .time{height:0.24rem; line-height:0.24rem; margin:0.26rem 0; }
.time p{ float:left;position:relative;}
.time span{width:0.33rem;height:0.32rem; position:absolute; top:-0.02rem;right:-0.35rem;}
.time span img{width:100%;height:100%;}

.cont .text-box{ font-size:0.25rem;}
.text-box p{ line-height:0.45rem; margin-bottom:0.1rem;}
.foot-btn{width:100%;height:0.8rem; background:#fff; border-top:1px solid #e8eaec; position:fixed; left:0;bottom:0;}
.foot-btn ul{width:6.4rem; margin:0 auto;height:0.52rem; margin-top:0.16rem;}
.foot-btn ul li{ float:left;}
.foot-btn ul li a{width:100%;height:100%; display:block;}
.foot-btn ul .say{width:2.03rem;height:0.52rem; border-right:1px solid #e8eaec;}
.say i{width:0.36rem;height:0.26rem; background:url(../img/say.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.7rem; margin-top:0.13rem;}
.say span{height:0.26rem; float:left; line-height:0.26rem; margin-left:0.16rem; margin-top:0.13rem;}
.foot-btn ul .zan{width:1.86rem;height:0.52rem; border-right:1px solid #e8eaec;}
.zan i{width:0.36rem;height:0.28rem; background:url(../img/zan1.png) no-repeat 0 0; background-size:100%; float:left; margin-left:0.54rem; margin-top:0.13rem;}
.zan span{height:0.26rem; float:left; line-height:0.26rem; margin-left:0.16rem; margin-top:0.13rem;}
.foot-btn ul .xing{width:1.2rem;height:0.52rem; border-right:1px solid #e8eaec;}
.xing i{width:0.34rem;height:0.24rem; margin:0 auto; display:block; padding-top:0.1rem;}
.xing i img{width:100%;}
.foot-btn ul .fx{width:1.25rem;height:0.52rem;}
.fx i{width:0.33rem;height:0.08rem;display:block; margin:0 auto; padding-top:0.15rem;}
.fx i img{width:100%;}


.zan1{width:1.7rem;display:flex;justify-content: space-around;height:0.5rem;border-radius:0.05rem;
    font:0.24rem/0.5rem "";background:rgb(197, 248, 255);color:rgba(0, 16, 238, 0.466);align-items: center;}
.zan2{width:0.8rem;height:0.5rem;background:rgb(197, 248, 255);color:rgba(18, 22, 245, 0.582);display:flex;
       align-items: center;justify-content: center;margin-left:0.2rem;border-radius:0.05rem}
       
.zan3{display:flex;justify-content:center;height:0.5rem;align-items: center;
   font:0.24rem/0.5rem "";color:rgba(46, 45, 45, 0.582);align-items: center;margin-left:0.2rem}
.zan4{width:0.28rem;display:flex;justify-content: space-around;height:0.5rem;align-items: center;
   font:0.24rem/0.5rem "";color:rgba(46, 45, 45, 0.582);align-items: center;margin-left:0.2rem}
   